@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  display: flex;
  justify-content: center;
  font-family: 'Poppins';
  width:100%
}
.nav{
    width:25%;
    height:auto;
    padding: 0 5vw;
    background: #fafbfd;
}@media screen and (max-width:500px){
  .nav{width:35%; padding-right: 8vw;}
}


.nav-title{
    text-align: left;
    margin: 45px 0 120px 0;
}

.nav-title p{color:#393c4d; font-weight: 600; width:100%}

.nav-title span{color:#f87051}

.nav-options{
    height:auto;
}

.nav-options ul{list-style-type: none; padding: 0;}

ul li{font-size:15px; text-align: left; color:#b5b5b6; font-weight: 400; cursor:pointer; margin-bottom: 30px;}

ul li:hover{color:#2b2f4d;}

li.active{color:#2b2f4d; font-weight: 600;}
.btn-container{
    width:75%;
    padding: 0 5vw;
    margin-top: 30px;
    height: auto;
}@media screen and (max-width:500px){
    .btn-container{width:50%}
  }
  

.btn-container h2{font-size: 30px; font-weight: 400; text-align:  left;}

.btn-container p,a{width:100%; font-size: 13px; text-align: left; color:#bbbbbb;}


footer{align-self: center; }

footer p{text-align: center; margin: 40px 0 10px 0;}

footer p a{
    margin: 0; 
    color:#f87051; 
    text-decoration: none;
}

.btn-list{
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 20px;
}
.btn{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    margin: 0 10px 15px 0;
}

.btn p{font-size: 11px; font-weight: 500!important; margin-bottom: 15px;}

button{
    border-radius:5px;
    outline:none;
    border: none;
    background: none;
    font-weight: 500;
    font-family: 'Poppins';
    letter-spacing: 0.3px;
    margin-right:9vw ;
}

.default{background: #e0e0e0; color:#5e5e5e}
.default:hover{background:#aeaeae; cursor:pointer; color:#575757}

.primary{background: #2962ff; color:#ffffff}
.primary:hover{background: #0039cb!important; cursor: pointer;}

.secondary{background: #455a64; color:#ffffff}
.secondary:hover{background:#1c313a!important; cursor: pointer; }

.danger{background: #d32f2f; color:#ffffff}
.danger:hover{background: #9a0007!important;  cursor:pointer}

.variantText{background: none!important; border:none; color:#2962ff}
.variantText:hover{background: #eaefff!important;  cursor: pointer;}
.textHover:hover{background: none!important; cursor:not-allowed}

.variantOutline{border:1px solid #2962ff; color:#2962ff}
.variantOutline:hover{background: #eaefff; cursor:pointer }

.btnShadow{box-shadow: 1px 1px 5px rgb(0, 0, 0,0.2);}

.disabled{background:#e0e0e0; color:#b9b9b9; cursor:not-allowed}

.btnSmall{padding:5px; height:30px; width:75px}

.btnMid{padding:5px; height:35px; width:90px}

.btnLarge{padding:5px; height:45px; width:97px; cursor:}

.startIcon::before{
    font-family: 'Material Icons Round';
    font-size: 1rem;
    content: 'add_shopping_cart';
    margin-right: 5px;
    position: relative;
    top: 3px;
}

.endIcon::after{
    font-family: 'Material Icons Round';
    font-size: 1rem;
    content: 'add_shopping_cart';
    margin-left: 5px;
    position: relative;
    top: 3px;
}

.default-hover-focus{background:#aeaeae;color:#5e5e5e; cursor: default!important; }

.outline-hover-focus{background: #eaefff; cursor: default!important;   }

.text-hover-focus{background: #eaefff!important;   cursor: default!important; }

.primary-hover-focus{background: #0039cb!important; cursor: default!important;  }

.sec-hover-focus{background:#1c313a!important; cursor: default!important;  }

.danger-hover-focus{background: #9a0007!important; cursor: default!important;  }
